import React from "react";
import { Redirect } from "react-router";
import { Container, Row, Col } from "react-bootstrap";
import Pure from "./Pure";
import v0 from "../images/villages/v0.png";
import client from "./CLASSES/client";
import "./Village.css";
import { Link } from "react-router-dom";
import { socket } from "./SocketIO";
import { ActiveVillage } from "./CLASSES/Types";

class Village extends Pure {
    /** @type {ActiveVillage} */
    activeVillage;
    state = {
        update: 0,
    }
    onReady() {
        client.village(village => {
            this.activeVillage = village;
            this.updateState({ update: Date.now() });
        });
    }
    render() {
        if (!client.ready)
            return <></>;
        if (!client.authorized)
            return <Redirect to="/Home" />;
        return <div className="VillageView">
            {/* <h2 className="VillageName">خريطة</h2> */}
            <h2 className="VillageName">خريطة القرية</h2>
            <Container>
                <Row>
                    <Col>
                        <div className="feilds">
                            <img src={v0} alt="الحقول" useMap="#feilds" />
                            <map name="feilds">
                                <Link to="/Building?id=1"><area coords="101,33,28" shape="circle" title="حقل الخشب المستوى 0" alt="حقل الخشب المستوى 0" /></Link>
                                <Link to="/Building?id=2"><area coords="165,32,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=3"><area coords="224,46,28" shape="circle" title="حقل الخشب المستوى 0" alt="حقل الخشب المستوى 0" /></Link>
                                <Link to="/Building?id=4"><area coords="46,63,28" shape="circle" title="حقل الحديد المستوى 0" alt="حقل الحديد المستوى 0" /></Link>
                                <Link to="/Building?id=5"><area coords="138,74,28" shape="circle" title="حقل الطين المستوى 0" alt="حقل الطين المستوى 0" /></Link>
                                <Link to="/Building?id=6"><area coords="203,94,28" shape="circle" title="حقل الطين المستوى 0" alt="حقل الطين المستوى 0" /></Link>
                                <Link to="/Building?id=7"><area coords="262,86,28" shape="circle" title="حقل الحديد المستوى 0" alt="حقل الحديد المستوى 0" /></Link>
                                <Link to="/Building?id=8"><area coords="31,117,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=9"><area coords="83,110,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=10"><area coords="214,142,28" shape="circle" title="حقل الحديد المستوى 0" alt="حقل الحديد المستوى 0" /></Link>
                                <Link to="/Building?id=11"><area coords="269,146,28" shape="circle" title="حقل الحديد المستوى 0" alt="حقل الحديد المستوى 0" /></Link>
                                <Link to="/Building?id=12"><area coords="42,171,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=13"><area coords="93,164,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=14"><area coords="160,184,28" shape="circle" title="حقل الخشب المستوى 0" alt="حقل الخشب المستوى 0" /></Link>
                                <Link to="/Building?id=15"><area coords="239,199,28" shape="circle" title="حقل القمح المستوى 0" alt="حقل القمح المستوى 0" /></Link>
                                <Link to="/Building?id=16"><area coords="87,217,28" shape="circle" title="حقل الطين المستوى 0" alt="حقل الطين المستوى 0" /></Link>
                                <Link to="/Building?id=17"><area coords="140,231,28" shape="circle" title="حقل الخشب المستوى 0" alt="حقل الخشب المستوى 0" /></Link>
                                <Link to="/Building?id=18"><area coords="190,232,28" shape="circle" title="حقل الطين المستوى 0" alt="حقل الطين المستوى 0" /></Link>
                                <Link to="/Center"><area coords="144,131,36" shape="circle" title="مركز القرية" alt="مركز القرية" /></Link>
                            </map>
                        </div>
                    </Col>
                    <Col>
                        <div className="movements"></div>
                        <div className="productions"></div>
                        <div className="troops"></div>
                    </Col>
                </Row>
            </Container>
        </div>;
    }
}

export default Village;